import React from "react";
import styled from "@emotion/styled";
import { padding } from "polished";
import { breakPoints } from "./Grid";

const Wrapper = styled("div")(({ maxWidth }) => ({
  display: "flex",
  flexDirection: "column",
  maxWidth,
  paddingTop: 81,
  paddingBottom: 90,
  width: "100%",

  "@media (max-width: 1200px)": {
    ...padding(20),
  },
  "@media (max-width: 745px)": {
    maxWidth: "100%",
  },
}));

const Icon = styled("img")(({ iconWidth, iconHeight, smallTitle }) => ({
  width: iconWidth,
  height: iconHeight,
  marginBottom: 24,
  [breakPoints.small]: {
    width: smallTitle ? 30 : iconWidth,
    height: smallTitle ? 30 : iconHeight,
  },
}));

const Title = styled("h3")(({ light, smallTitle, flexIconsAndTitle }) => ({
  fontSize: 32,
  lineHeight: "34px",
  fontWeight: 600,
  paddingBottom: 24,
  marginBottom: 0,
  color: light ? "#FFF" : "#000",
  [breakPoints.small]: {
    margin: flexIconsAndTitle ? "0px 0px 15px 25px" : {},
    fontSize: smallTitle ? 22 : 32,
  },
}));

export const sizes = {
  medium: 48,
  large: 64,
};

export const FlexWrapper = styled("div")(({ flexIconsAndTitle }) => ({
  display: "block",
  [breakPoints.small]: {
    display: flexIconsAndTitle ? "flex" : "block",
    flexDirection: flexIconsAndTitle ? "row" : "initial",
  },
}));

const SimpleValueProp = ({
  icon,
  title,
  content,
  maxWidth = 300,
  light = false,
  size,
  smallTitle = false,
  flexIconsAndTitle = false,
}) => (
  <Wrapper maxWidth={maxWidth}>
    <FlexWrapper flexIconsAndTitle={flexIconsAndTitle}>
      {icon && (
        <Icon
          src={icon}
          iconWidth={size}
          iconHeight={size}
          smallTitle={smallTitle}
        />
      )}
      <div>
        <Title
          light={light}
          smallTitle={smallTitle}
          flexIconsAndTitle={flexIconsAndTitle}
        >
          {title}
        </Title>
      </div>
    </FlexWrapper>
    {content}
  </Wrapper>
);

export default SimpleValueProp;
